<template>
    <div class="car-desc">
        <section class="container">
            <div class="row">
                <div class="col-xs-12">
                    <v-breadnav>{{`${importCarDesc.carName}  ${importCarDesc.carSeriesName}`}}</v-breadnav>
                </div>
            </div>       
            <div class="row">
                <div class="col-xs-12">
                    <section class="desc-header clearfix">
                        <div class="col-xs-5">
                            <v-slider :slideArr = "getSliderArr"></v-slider>
                        </div>
                        <div class="col-xs-7">
                            <h3>{{`${importCarDesc.carName}  ${importCarDesc.carSeriesName}`}}</h3>
                            <p class="price-refer">本报价参考：</p>
                            <ul class="price-desc clearfix">
                                <li class="current-price">
                                    <p>￥<span class="price-num">{{importCarDesc.referenPrice}}</span></p>
                                    <p>参考价 <i class="glyphicon glyphicon-question-sign"></i></p>
                                </li>
                                <li class="old-price">
                                    <p><del>￥<span class="price-num">{{importCarDesc.marketPrice}}</span></del></p>
                                    <p>市场价 <i class="glyphicon glyphicon-question-sign"></i></p>
                                </li>
                                <li class="save-price">
                                    <p>￥<span class="price-num">{{importCarDesc.maxSavePrice}}</span></p>
                                    <p>最高节省</p>
                                </li>
                            </ul>
                            <div class="color-list">
                                <span>车身颜色：</span>
                                <span class="color" v-for="(item,index) in getColorArr" :key="index" :style="{background:item}"></span>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
            <div class="row">
                <div class="col-xs-12">
                    <section class="desc-prefer ">
                        <ul class="prefer-list clearfix">
                            <li>
                                <p class="prefer-title">参考底价</p>
                                <p>￥<span class="price-num">{{importCarDesc.referenPrice}}</span></p>
                            </li>
                            <li>
                                <p class="prefer-title">优惠条件</p>
                                <p>凭单优惠（预购单）至少5000元</p>
                            </li>
                            <li>
                                <p class="prefer-title">服务优势</p>
                                <p>4s经销商或指定服务商售后挂牌按揭一站式汽车金融服务</p>
                            </li>
                            <li>
                                <p class="prefer-title">热度</p>
                                <p><span>360000</span>人查看过</p>
                            </li>
                            <li>
                                <button class="btn btn-default">预约到店</button>
                            </li>
                        </ul>
                    </section>
                </div>
            </div>
            <div class="row">
                <div class="col-xs-12">
                    <section class="car-detail " v-if="importCarDesc" data-target="#fixed-navbar" data-offset="0">
                        <nav>
                            <a href="#base-config" class="base-config active">基本配置</a>
                            <a href="#questions" class="questions">常见问题</a>
                            <a href="#car-details" class="car-desc">车辆信息</a>
                        </nav>
                        <article>
                            <div class="title" id="base-config">
                                <h3>基本配置</h3>
                                <a class="more pull-right" data-toggle="modal" data-target="#config-modal">查看全部</a>
                            </div>
                            <ul class="configs clearfix">
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车身机构</label>
                                        <span>{{importCarDesc.cheshenjiegou}}</span>
                                    </div>                                
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>长/宽/高</label>
                                        <span>{{`${importCarDesc.length}/${importCarDesc.width}/${importCarDesc.height}mm`}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>发动机</label>
                                        <span>{{importCarDesc.fadongjixinghao}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>变速箱</label>
                                        <span>{{importCarDesc.biansuxiangtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>驱动方式</label>
                                        <span>{{importCarDesc.qudongtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>燃料形式</label>
                                        <span>{{importCarDesc.ranliaotype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>综合油耗</label>
                                        <span>{{importCarDesc.cheshenjiegou}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车辆规格</label>
                                        <span>{{importCarDesc.classType}}</span>
                                    </div>
                                </li>
                            </ul>
                            <div class="title" id="questions">
                                <h3>常见问题</h3>
                            </div>
                            <div class="question-img">
                                <img :src="importCarDesc.oftenQuestionUrl" alt="" class="img-responsive">
                            </div>
                            <div class="title" id="car-details">
                                <h3>车辆信息</h3>
                            </div>
                            <div class="car-detail-iframe" v-html="importCarDesc.descUrl">
                            </div>
                        </article>
                    </section>
                </div>
            </div>
        </section>
        <section class="fixed-nav" v-show="scrollHeight>=620">
            <nav class="navbar navbar-default navbar-fixed-top">
                <div class="container">
                    <ul class="nav navbar-nav" id="fixed-navbar">
                        <li><a href="#base-config" class="active" >基础配置</a></li>
                        <li><a href="#questions">常见问题</a></li>
                        <li><a href="#car-details">车辆信息</a></li>
                    </ul>
                    <button class="btn btn-default pull-right">预约到店</button>
                </div>
            </nav>
        </section>
        <section class="config-modal">
            <v-config :config ="configArr"></v-config>
        </section>
    </div>       
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vSlider from "@/components/pc/slider"
import vConfig from "@/components/pc/carConfigModal"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            carId:this.$route.params.id,
            importCarDesc:{},
            scrollHeight:0,
            configArr:{}
        }
    },
    components:{
        vBreadnav,
        vSlider,
        vConfig 
    },
    methods:{
        getDescData:function(){
            let data = {};
            let _this = this;
            data.cartId = this.carId;
            data.platForm = "020003";
            $.ajax({
                url:PC_COMMONURL+'/queryImportCarDetail',
                type:"get",
                data:data,
                dataType:"json",
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.importCarDesc = data.data.carDetail;
                        _this.configArr = Object.assign(data.data.params1,data.data.params2);
                    }
                }
            })
        }
    },
    computed:{
        getSliderArr:function(){          
            let newSliderArr = [];
            if(Object.keys(this.importCarDesc).length !=0){
                let slideArr = this.importCarDesc.icons;
                slideArr.forEach(function(n){
                    let obj = new Object();
                    obj.imgUrl = n;
                    newSliderArr.push(obj);
                })               
            }
            return newSliderArr;
        },
        getColorArr:function(){
            if(Object.keys(this.importCarDesc).length !=0){
                let colorArr = this.importCarDesc.colors.split(','); 
                return colorArr;         
            }           
        }
    },
    mounted:function(){
        this.getDescData();
         let _this = this;
        $(document).scroll(function(){
            let scrollHeight = $(document).scrollTop();
            _this.scrollHeight = scrollHeight;
            if($("#base-config").offset() && $("#questions").offset() && $("#car-details").offset()){
                let baseHeight = $("#base-config").offset().top;
                let questionHeight =$("#questions").offset().top;
                let carDescHeight = $("#car-details").offset().top;
                if(baseHeight <= scrollHeight && scrollHeight < questionHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(0).find('a').addClass("active");
                }else if(questionHeight <= scrollHeight && scrollHeight < carDescHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(1).find('a').addClass("active");
                }else if(carDescHeight <= scrollHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(2).find('a').addClass("active");
                }
            }
            
        })  
    }
    
}
</script>



